.standarts {
    position: relative;
    overflow: hidden;

    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: -50%;
        width: 100%;
        height: 100%;
        z-index: 0;
        background-color: $blue;

        @include media ("<tablet") {
            display: none;
        }
    }

    .standarts_content {
        display: flex;
        align-items: center;
        justify-content: space-around;

        @include media ("<tablet") {
            flex-direction: column;
        }
    }

    .business,
    .methods {
        width: 45%;
        @include adaptiv-vaule("padding-top", 100, 80, 1);
        @include adaptiv-vaule("padding-bottom", 100, 80, 1);

        @include media ("<tablet") {
            width: 100%
        }
    }

    .business {
        position: relative;
        padding-right: 20px;

        @include media ("<tablet") {
            padding-left: 0;

            &::before {
                content: '';
                position: absolute;
                top: 0;
                left: -10%;
                width: 150%;
                height: 100%;
                z-index: 0;
                background-color: $blue;
            }
        }


    }

    .puzzle {
        position: absolute;
        top: 0;
        right: -100px;
    }

    .business_title {
        color: $white;
        font-weight: 600;
        line-height: 135%;
        letter-spacing: -0.02em;
        max-width: 425px;
        @include adaptiv-vaule("margin-bottom", 25, 20, 1);
        z-index: 1;
        position: relative;
    }

    .business_descr {
        font-weight: 500;
        @include adaptiv-vaule("font-size", 16, 14, 1);
        line-height: 175%;
        color: $gray;
        max-width: 530px;
        z-index: 1;
        position: relative;
    }

    .methods {
        padding-left: 20px;

        @include media ("<tablet") {
            padding-left: 0;
        }
    }

    .methods_title {
        font-weight: 600;
        line-height: 135%;
        letter-spacing: -0.02em;
        color: $blue;
        @include adaptiv-vaule("margin-bottom", 25, 20, 1);

        @include media ("<phone") {
            max-width: 250px;
        }
    }

    .methods_item {
        padding-left: 30px;
        color: $black;
        @include adaptiv-vaule("font-size", 16, 14, 1);
        line-height: 20px;
        font-weight: 500;
        position: relative;

        @include media ("<tablet") {
            max-width: 285px;
        }

        &::before {
            content: url(../images/cards/check.svg);
            position: absolute;
            left: 0;
            top: 65%;
            transform: translateY(-50%);
        }

        &:not(:last-child) {
            margin-bottom: 12px;
        }
    }
}

