        display: block;
    } */ }
  @media (max-width: 479px) {
    header {
      padding: 10px 0 15px 0; } }
  header .header_content,
  header .nav-menu {
    display: flex;
    align-items: center;
    justify-content: space-between; }
  header .logo {
    /* Только если меньше контейнера */
    width: 206px; }
    @media (max-width: 1350px) {
      header .logo {
        width: calc(150px + 56 * ((100vw - calc(375px)) / 975)); } }
  @media (max-width: 1023px) {
    header .nav {
      display: none; } }
  header .menu-item {
    position: relative;
    /* Только если меньше контейнера */
    margin-left: 20px; }
    @media (max-width: 1350px) {
      header .menu-item {
        margin-left: calc(1px + 19 * ((100vw - calc(375px)) / 975)); } }
    header .menu-item:hover:after {
      content: '';
      position: absolute;
      width: 100%;
      height: 1px;
      left: 0;
      bottom: -5px;
      background-color: #031C77;
      animation: bottom_border .5s linear; }
  header .menu_link {
