    @media (max-width: 1350px) {
      header .menu_link {
        font-size: calc(5px + 11 * ((100vw - calc(375px)) / 975)); } }
  header .item_active {
    color: #031C77; }
  header .phone {
    display: flex;
    align-items: center;
    /* Только если меньше контейнера */
    margin-left: 20px; }
    @media (max-width: 1350px) {
      header .phone {
        margin-left: calc(1px + 19 * ((100vw - calc(375px)) / 975)); } }
    @media (max-width: 1023px) {
