    font-size: 18px; }
    @media (max-width: 1350px) {
      header .phone_number {
        font-size: calc(5px + 13 * ((100vw - calc(375px)) / 975)); } }
  header .burger_menu {
    display: none; }
    @media (max-width: 1023px) {
      header .burger_menu {
        display: block;
        width: 24px;
        height: 18px;
        position: relative; }
        header .burger_menu::after, header .burger_menu::before,
        header .burger_menu span {
          content: '';
          position: absolute;
          width: 100%;
          height: 1px;
          background-color: #3D3E45;
          transition: all .3s linear; }
        header .burger_menu::after {
          left: 0;
          bottom: 0; }
        header .burger_menu::before {
          top: 0;
          left: 0; }
        header .burger_menu span {
          top: 8.5px;
          left: 0;
          transition: all .3s linear; } }
  header .burger_active::before {
    top: 50%;
    left: 50%;
    transform: translateY(-50%) translateX(-50%) rotate(45deg);
    transition: all .3s linear; }
  header .burger_active::after {
    top: 50%;
    left: 50%;
    transform: translateY(-50%) translateX(-50%) rotate(-45deg);
    transition: all .3s linear; }
  header .burger_active span {
    width: 0;
    transition: all .3s linear; }

